.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: auto;
  justify-content: center;
  margin: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  background-color: #fff;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  transition: transform .3s;
}

.photo-card .info {
  color: #fff;
  background-color: rgba(0, 0, 0, .8);
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.photo-card .info p {
  margin: 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more:hover {
  background-color: #0056b3;
}

.search-form, .load-more {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  margin: 40px auto 20px;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

